import emptyTips from '@/components/emptyTips';
import LingLoading from '@/components/attendance/LingLoading';
export default {
  props: {
    userType: {
      Number,
      default: 0
    },
    userId: {
      Number,
      default: 0
    },
  },
  data() {
    return {
      features:[],
      showTips: 0,
      state: {
        isShow: true,
        message: '加载中',
        state: 'loading'
      },
    }
  },
  created() {
    this._initFeatureList();
  },
  methods: {
    _initFeatureList() {
      // 获取首页功能列表
      this.$http.get(`${WX_URL}base/function/getAreaItems/${this.userType}`).then((response) => {
        response = response.data;
        if (response.success) {
          this.state.isShow = false;
          this.features = response.data;
          if (!this.features.length) {
            this.showTips = 1;
          } else {
            this.showTips = 0;
          }
        }
      },(response) => {
        this.state.state = 'error';
        this.state.message = '网络错误';
      });
    },
    // 选择进入功能列表
    chooseFeature(val) {
      if (this.userType === PARENT) {
        this.$router.push('/mailContent');
      } else {
        this.$router.push('/principal/mailboxList');
      }

    },
    // 查看更多功能列表
    chooseMoreFeature() {
      console.log("111");
    },
    fetchData() {
      this._initFeatureList();
    }
  },
  components: {
    emptyTips,
    LingLoading
  }
}




