import BScroll from 'better-scroll';
export default {
  data() {
    return {
      flag: {
        showFlag: false,
        iconShowFlag: true, //选择icon显示
        listShowFlag: false, //选择列表显示
      },
      userList: [],
      selectItem: 0,
      scrollY: 0
    }
  },
  props: {
    userType: {
      Number,
      default: 0
    },
    chooseType: {
      String,
      default: ''
    }
  },
  created() {
    //获取localStorage
    this.userList = JSON.parse(localStorage.getItem('USERLIST'));
    // 默认显示第一个
    this.$emit('userData',this.userList.list[0]);

  },
  computed: {
    // 当前页面路由地址
    currentRouter() {
      let currentUrl = this.$store.state.route.path;
      switch(currentUrl) {
          case '/parent/parentIndex':
          case '/principal/principalIndex':
          case '/teacher/teacherIndex':
           this.flag.showFlag = true;
           break;
         default:
           this.flag.showFlag = false;
           break;
      }
    }
  },
  methods: {
    showUserList() {
      this.setShowFlag();
      this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.userListWrapper,{
            startY: this.scrollY,//设置初始的Y位置
            click: true,
            probeType: 3
          });
          // 获取当前的Y位置
          this.scroll.on('scroll', (pos) => {
            this.scrollY = Math.round(pos.y);
          });
      });
    },
    chooseUser(user,index) {
      this.$emit('userData', user);
      this.selectItem = index;
      this.setShowFlag();
    },
    setShowFlag() {
      if(!this.flag.iconShowFlag) {
        setTimeout(() => {
          this.flag.iconShowFlag = !this.flag.iconShowFlag;
        },200);
      }else {
        this.flag.iconShowFlag = !this.flag.iconShowFlag;
      }
      this.flag.listShowFlag = !this.flag.listShowFlag;
    }
  }
}



