
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import message from '@/components/index/message';
import emptyTips from '@/components/emptyTips';
import LingLoading from '@/components/attendance/LingLoading';
export default{
props: {
userId: {
Number,
default: 0
},
userType: {
Number,
default: 0
}
},
data() {
return {
messages:[],
messageInfo: {},
showTips: 0,
avatarUrl: 'http://cdn-didano.oss-cn-shenzhen.aliyuncs.com/xiaonuo/wx/img/logo_2.jpg',
state: {
isShow: true,
message: '加载中',
state: 'loading'
},
sheetShow: false,
sheetMenus: {
menu1: '确定要删除该消息？',
menu2: '<span style="color:red">删除</span>'
},
deleteMessageId: ''
}
},
created() {
this._initMessageList();
},
methods: {

_initMessageList() {
this.$http.post(`${WX_URL}base/notice/post/notice_findtByUserid/${this.userId}/${this.userType}`).then((response) => {
response = response.data;
if(response.success){
this.state.isShow = false;
this.messages = response.data;
if (!this.messages.list.length) {
this.showTips = 1;
} else {
this.showTips = 0;
}
}else{

console.log("code:1001");
}
},(response) => {
this.state.state = 'error';
this.state.message = '网络错误';
});
},

isRead(noticeId,index) {
this.$http.post(`${WX_URL}base/notice/post/setNoticeRead`,{'noticeId':noticeId,'userId':this.userId}).then((response) => {
response = response.data;
if(response.success){

this.messages.list[index].is_read = 1;
}else{

console.log("code:1002");
}
});
},

selectMessage(message,index) {

if (message.is_read == 0) {
this.isRead(message.id,index);
}

if (message.noticeModel === 0) {
this.messageInfo = message;
this.$refs.message.show();

} else if (message.noticeModel === 1) {
this.$router.push({ path: '/mailContent', query: { mailId: message.sourceId , dialogueTitle:`与${message.title}进行沟通`}});

} else if (message.noticeModel === 2) {

if ((message.redirectUrl.startsWith('http') || message.redirectUrl.startsWith('https'))) {
window.location.href = message.redirectUrl;
} else {
window.location.href = `http://${message.redirectUrl}`;
}
}
},

deleteMessage(message) {
this.sheetShow = true;
this.deleteMessageId = message.id;
},
SheetClick(key) {
if( key === 'menu2') {
this.$http.post(`${WX_URL}base/notice/post/deleteNoticeByUser`,{'noticeId':this.deleteMessageId,'userId':this.userId}).then( (response) => {
response = response.data;
if( response.success ) {
this._initMessageList();
}
});
}
},
fetchData() {
this._initMessageList();
}
},
watch: {

'userId':function(val){
this.userId = val;
this._initMessageList();
}
},

components: {
message,
emptyTips,
LingLoading,
Actionsheet
}
};



